@import '../../assets/scss/_vars.scss';

.achievements {
  // bem
  padding-top: 100px;
  padding-bottom: 100px;

  &__inner {
  }

  &__top {
    display: flex;
    align-items: flex-end;
    margin-bottom: 16px;
  }

  &__title {
    flex: 1 1 auto;
    font-weight: 700;
    font-size: 64px;
    line-height: 77px;
  }

  &__button {
    background-color: $main;
    border-radius: 16px;
    padding: 16px 34px;
    font-size: 24px;
    line-height: 24px;
    color: $white;
    font-weight: 700;

    span {
      font-size: inherit;
      line-height: inherit;
      font-weight: inherit;
    }
  }

  &__subtitle {
    font-size: 24px;
    line-height: 24px;
    margin-bottom: 40px;
    color: $grayText;
    letter-spacing: normal;
  }

  &__elems {
  }

  &__elem {
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.08), 0px 4px 6px rgba(0, 0, 0, 0.16);
    border-radius: 16px;
    padding: 24px;
    width: 306px;
    display: flex;
    flex-direction: column;
  }

  &__elem-content {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 22px;
    flex: 1 1 auto;
    letter-spacing: normal;
  }

  &__elem-date {
    font-size: 16px;
    line-height: 16px;
    color: $grayText;
    letter-spacing: normal;
  }
}

.achievementsSwiper {
  width: calc(100% + 80px + 24px);
  max-width: calc(1440px - 80px);
  overflow: clip;
  overflow-y: visible;
  height: 100%;

  &::before {
    content: '';
    z-index: 5;
    position: absolute;
    top: 0;
    right: -2px;
    height: 100%;
    width: calc(80px + 24px);
    background: linear-gradient(
      270deg,
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0) 100%
    );
  }
}

.swiper-slide:first-child {
  margin-left: 3px;
}

.swiper-slide {
  height: auto;
}
